.list {
  list-style-type: none;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-y: auto;
}

.list::-webkit-scrollbar {
  background-color: #2F2F37;
  width: 8px;
}

.list::-webkit-scrollbar-thumb {
  background-color: #8585AD;
}

.link {
  text-decoration: none;
  color: #F2F2F3;
}